.card {
  background-size: cover;
  background-image: -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 0, 0.7)), to(rgba(0, 0, 0, 0.7))), url("../images/card-bg.png");
  background-image: -webkit-linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)), url("../images/card-bg.png");
  background-image: -o-linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)), url("../images/card-bg.png");
  background-image: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)), url("../images/card-bg.png");
  word-wrap: break-word;
  -moz-hyphens: auto;
}

.card-sub .material-icons {
  font-size: 18px;
}

.card-sub span {
  font-size: 18px;
  margin-right: 10px;
}

.card-content p {
  padding-top: 15px;
}

.card .card-action a{
	-webkit-transition: .3s ease-out;
	-o-transition: .3s ease-out;
	transition: .3s ease-out;	
}

.card .card-action a:hover{
	color: #ffffff !important;
}

.card img.octicons {
  display: inline-block;
  vertical-align: text-top;
}



