#ErrorMessages {
  overflow: hidden;
  padding: 0 .75rem;
}

#ErrorMessages.display-error {
  -webkit-animation: div-grow 2s;
  animation: div-grow 2s;
}

#ErrorMessages.remove-error {
  -webkit-animation: div-shrink 1s;
  animation: div-shrink 1s;
}


@-webkit-keyframes div-grow {
  0%   { max-height: 0; }
  100% { max-height: 100px; }
}
@keyframes div-grow {
  0%   { max-height: 0; }
  100% { max-height: 100px; }
}

@-webkit-keyframes div-shrink {
  0%   { max-height: 100px; }
  100% { max-height: 0; }
}
@keyframes div-shrink {
  0%   { max-height: 100px; }
  100% { max-height: 0; }
}



